import React, { Component } from 'react';

export class Category extends Component {
    render() {
        let class_name = "category";
        class_name += this.props.less_margin? " usecases__margin10" : "";

        return (
            <div className={class_name} id={this.props.id}>
                <div className="category__title">
                    {this.props.title}
                </div>
                <div className="category__subtitle">
                    {this.props.subtitle}
                </div>
                <div className="category_content">
                    {this.props.children}
                </div>
            </div>
        )
    }
}