import React, { Component} from 'react';
import {DirtyHandsScreen} from "../usecases/DirtyHandsScreen";
import {EyeCureScreen} from "../usecases/EyeCureScreen";
import {MusicAdminScreen} from "../usecases/MusicAdminScreen";
import cookie from "react-cookies";
import strings from "../Localisation";
import idGenerator from "react-id-generator";
import {ButtonLink} from "../interactive/ButtonLink";
import {EyeControlScreen} from "../usecases/EyeControlScreen";
import {BookReaderScreen} from "../usecases/BookReaderScreen";
import {TakePhotoScreen} from "../usecases/TakePhotoScreen";
import {StartingBuildScreen} from "../usecases/StartingBuildScreen";
import {ShowingPresentationScreen} from "../usecases/ShowingPresentationScreen";
import {TurnOffLightScreen} from "../usecases/TurnOffLightScreen";
import {SmartGlassesScreen} from "../usecases/SmartGlassesScreen";

export class UsecasesScreen extends Component {
    constructor(props) {
        super(props);
        this.state = {
            count: 3
        };
        this.set_count = 0;
        this.button_id__more = idGenerator();
    }

    componentDidMount() {
        let button__more = document.getElementById(this.button_id__more);
        let link = this;

        button__more.addEventListener("click", function(event) {
            event.preventDefault();
            let current_count = link.state.count;
            link.set_count = 0;

            link.setState({
                count: current_count + 2
            });
        });
    }

    verifyComponent(component) {
        this.set_count += 1;
        return this.state.count >= this.set_count? component : <div></div>;
    }

    render() {
        return (
            <div className="usecases">
                { this.verifyComponent(<DirtyHandsScreen />) }
                { this.verifyComponent(<EyeCureScreen />) }
                { this.verifyComponent(<EyeControlScreen />) }
                { this.verifyComponent(<MusicAdminScreen />) }
                { this.verifyComponent(<StartingBuildScreen />) }
                { this.verifyComponent(<BookReaderScreen />) }
                { this.verifyComponent(<ShowingPresentationScreen />) }
                { this.verifyComponent(<TurnOffLightScreen />) }
                { this.verifyComponent(<SmartGlassesScreen />) }
                { this.verifyComponent(<TakePhotoScreen />) }

                <ButtonLink
                    fullpage={true}
                    margin_left={true}
                    id={this.button_id__more}
                    text={strings.usecases__show_more} />
            </div>
        )
    }
}